body {
  font: menu;
  font-size: 100%;
  margin: 5rem auto;
  max-width: 720px;
  line-height: 1.7;
  background-color: whitesmoke;
}

.hovercard {
  border-bottom: 1px solid;
  color: #69e;
  cursor: help;
}
.hovercard-element {
  box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.2) !important;
  background-color: #fff !important;
}
[x-placement="top"] + .hovercard-arrow::before {
  border-top: 10px solid #fff !important;
}
.hovercard-arrow::before {
  border-bottom: 10px solid #fff !important;
}

pre {
  background-color: rgba(0, 0, 0, 0.1);
  padding: 1rem 1.25rem;
}

code {
  background-color: rgba(0, 0, 0, 0.1);
  font-size: 101%;
  color: #c33;
  padding: 0.05rem 0.25rem;
  margin: 0 0.15rem;
  vertical-align: middle;
  display: inline-block;
}

.hovercard-element .hovercard-description::after {
  background: linear-gradient(transparent, #fff) !important;
}

.logo {
  height: 3rem;
  float: left;
  margin-right: 1rem;
  vertical-align: middle;
}
.hovercard {
  white-space: nowrap;
}

.hovercard-element {
  position: absolute;
  display: none;
  text-decoration: none;
  color: inherit;
  background-color: whitesmoke;
  width: 330px;
  padding: 15px 20px;
  border-radius: 5px;
  box-sizing: border-box;
  z-index: 15;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  max-height: 200px;
  overflow: hidden;
}

.hovercard-element.hovercard-has-image {
  width: 400px;
}

.hovercard-arrow {
  position: absolute;
  z-index: 17;
  height: 20px;
  width: 330px;
  box-sizing: border-box;
  display: none;
}
.hovercard-element.hovercard-has-image + .hovercard-arrow {
  width: 400px;
}

.hovercard-arrow::before {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -5px;
  content: "";
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid whitesmoke;
}

[x-placement="top"] + .hovercard-arrow::before {
  border-bottom: none !important;
  bottom: auto;
  top: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid whitesmoke;
}

.hovercard-element.hovercard-visible,
.hovercard-arrow.hovercard-visible {
  display: block;
}

.hovercard-element .hovercard-title {
  font-size: inherit;
  margin-top: 0;
  margin-bottom: 1rem;
}
.hovercard-element .hovercard-description {
  margin-bottom: 0;
}
.hovercard-element.hovercard-has-image .hovercard-description {
  max-width: 210px;
}
.hovercard-element .hovercard-image {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 150px;
  background-size: cover;
  background-position: center;
}

.hovercard-element .hovercard-description::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 175px;
  height: 55px;
  background: linear-gradient(transparent, whitesmoke);
}
